.view-button {
    padding: 5px 4px;
    background-color: #eaeaea;
    border: none;
    cursor: pointer;
}

.meaning-button {
    padding: 5px 4px;
    background-color: rgb(87, 91, 95);
    color: rgb(87, 91, 95);
    border: none;
    word-wrap: break-word;
}

.view-button.active {
    background-color: #3e8e41;
}

.record-button.active {
    background-color: #3e8e41;
    color: white;
}

.record-button-done.active {
    background-color: rgb(76, 113, 69);
    color: white;
}

.record-button-done.onmouse {
    background-color: rgb(122, 183, 109);
    color: white;
}