header {
  width: 100%;
  box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
  border-bottom: 1px solid #e5e5e5;
  white-space: nowrap;
  min-height: 60px;
  text-align: center;
}

footer {
  width: 100%;
  box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
  border-top: 1px solid #e5e5e5;
  position: absolute;
  bottom: 0;
  min-height: 60px;
  /* line */
  white-space: nowrap;
  text-align: center;
}

#root{
  width: 80%;
  text-align: center;
}